@import "main-module";

.form {
    &__row {
        display: flex;
    }

    &__label {
        display: block;
        position: relative;
    }

    &__label-search {
        position: relative;
    }

    &__input {
        width: 100%;
        padding: 14px 20px;
        border-radius: 10px;
        border: 1px solid $color_grey_light;
        transition: border-color .3s linear;
        font-size: 14px;
        font-weight: 600;
        color: $color_black;
        &::placeholder {
            font-size: 14px;
            font-weight: 600;
            color: $color_black;
        }

        &:hover {
            border-color: $color_blue;
        }
    }

    &__input-search {
        position: absolute;
        top: 14px;
        right: 20px;
        svg {
            width: 16px;
            height: 16px;
        }
    }

    &__btn {
        
    }


}