@import "main-module";

.tabs {
    &__list {
        display: flex;
        align-items: center;
        background-color: #F7F9FB;
        padding: 6px;
        border-radius: 10px;

        @include below(768px) {
            overflow-y: hidden;
            overflow-x: auto;
        }
    }

    &__item {
        flex: auto;
    }

    &__btn {
        font-size: 14px;
        font-weight: 600;
        line-height: normal;
        color: #000;
        display: block;
        width: 100%;
        text-align: center;
        padding: 14px;
        border-radius: 10px;

        @include below(768px) {
            padding: 14px 20px;
            white-space: nowrap;
        }

        &_active {
            color: #416FF4;
            background-color: #fff;
            box-shadow: 0px 10px 10px -10px rgba(0, 0, 0, 0.15);
        }
    }

    &__numb {
        font-size: 10px;
        font-weight: 700;
        color: $color_white;
        background-color: $color_green;
        padding: 2px 6px;
        border-radius: 6px;
        margin-left: 10px;
    }
}