@import "main-module";

.also {
    background-color: #F7F9FB;
    padding: 40px 0;

    @include below(768px) {
        padding: 40px 0 80px;
    }

    &__row {
        display: flex;
        &.swiper {
            margin: -30px 0;
            padding: 30px 0;
        }

        .swiper-wrapper {
            display: flex;
            align-items: stretch;
        }

        .swiper-pagination {
            display: none;
            @include below(768px) {
                display: block;
            }
            &.swiper-pagination-horizontal {
                bottom: 0;
            }
            .swiper-pagination-bullet {
                width: 8px;
                height: 8px;
                margin: 0 5px;
                background-color: rgba(0, 0, 0, 0.20);
                opacity: 1;
                transition: transform .3s linear;
            }

            .swiper-pagination-bullet-active {
                transform: scale(1.3);
                background-color: $color_blue;
            }
        }
    }

    &__item {
        &.swiper-slide {
            display: flex;
            flex-direction: column;
            height: initial;
        }
    }

    &__title {
        font-size: 34px;
        font-weight: 700;
        margin-bottom: 20px;

        @include below(768px) {
            font-size: 32px;
        }
    }
}