@import "main-module";

.pagination {
    margin-top: 40px;

    &__list {
        display: flex;
        align-self: center;
        justify-content: center;
        margin: 0 -5px;
    }

    &__item {
        margin: 0 5px;
    }

    &__link {
        width: 44px;
        height: 44px;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 10px;
        border: 1px solid $color_black;
        color: $color_black;
        font-size: 14px;
        font-weight: 600; 
        transition: background-color .3s linear,color .3s linear,border-color .3s linear;

        &:hover:not(:active) {
            border: 1px solid  #DBDEE7;
            background-color: #F7F9FB;
            color: $color_grey;
        }

        &_active {
            border: 1px solid  #DBDEE7;
            background-color: #F7F9FB;
            color: $color_grey;
        }

        &_last {
            width: 85px;
            color: $color_blue;
            border-color: $color_blue;
        }
    }
}