@import "main-module";

.payment {
    padding-bottom: 100px;

    @include below(768px) {
        padding-bottom: 80px;
    }

    &__title {
        font-size: 44px;
        font-weight: 700;
        margin-bottom: 40px;

        @include below(768px) {
            font-size: 32px;
            margin-bottom: 20px;
        }
    }

    &__wrap {
        width: 100%;
        max-width: 636px;
        margin: 0 auto;
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 40px;
        border-radius: 20px;
        border: 1px solid #F7F9FB;
        background-color: $color_white;
        box-shadow: 0px 10px 20px -10px rgba(0, 0, 0, 0.15);

        @include below(768px) {
            padding: 40px 15px 15px 15px;
        }

        &-title {
            text-align: center;
            font-size: 24px;
            font-weight: 700;
            margin-bottom: 40px;

            br {
                display: none;

                @include below(479px) {
                    display: block;
                }
            }

            @include below(768px) {
               margin-bottom: 20px;
            }
        }

        &-btn {
            margin: 40px 0;
            width: 100%;

            @include below(768px) {
                margin: 20px 0 40px;
            }
        }

        &-text {
            font-size: 12px;
            font-weight: 500;
            line-height: 160%; 
            color: $color_grey;
        }

    }

    &__switch {
        margin-top: 40px;
    }

    &__row {
        display: flex;
        flex-wrap: wrap;
        align-items: stretch;
        margin: -20px -10px 0;

        @include below(768px) {
            margin: -15px -7.5px 0;
        }
    }

    &__item {
        width: calc(33.333% - 20px);
        margin: 20px 10px 0;
        height: 100px;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 20px;
        border: 1px solid $color_grey_light;
        transition: border-color .3s linear;

        &_active {
            border-color: $color_blue;
        }

        @include below(768px) {
            width: calc(50% - 15px);
            margin: 15px 7.5px 0;
            height: 90px;
        }

        &:hover {
            border-color: $color_blue;
        }

        &:nth-of-type(1) {
            svg {
                width: 103px;
                height: 52px;
                flex-shrink: 0;

                @include below(768px) {
                    width: 91px;
                    height: 46px;
                }
            }   
        }

        &:nth-of-type(2) {
            svg {
                width: 98px;
                height: 32px;
                flex-shrink: 0;

                @include below(768px) {
                    width: 86px;
                    height: 28px;
                }
            }
        }

        &:nth-of-type(3) {
            svg {
                width: 76px;
                height: 60px;
                flex-shrink: 0;

                @include below(768px) {
                    width: 84px;
                    height: 43px;
                }
            }
        }

        &:nth-of-type(4) {
            svg {
                width: 96px;
                height: 49px;
                flex-shrink: 0;

                @include below(768px) {
                    width: 66px;
                    height: 52px;
                }
            }
        }

        &:nth-of-type(5) {
            svg {
                width: 88px;
                height: 34px;
                flex-shrink: 0;

                @include below(768px) {
                    width: 74px;
                    height: 28px;
                }
            }
        }

        &:nth-of-type(6) {
            svg {
                width: 98px;
                height: 57px;
                flex-shrink: 0;

                @include below(768px) {
                    width: 86px;
                    height: 51px;
                }
            }
        }

        &:nth-of-type(7) {
            svg {
                width: 80px;
                height: 23px;
                flex-shrink: 0;

                @include below(768px) {
                    width: 72px;
                    height: 21px;
                }
            }
        }

        &:nth-of-type(8) {
            svg {
                width: 80px;
                height: 50px;
                flex-shrink: 0;

                @include below(768px) {
                    width: 68px;
                    height: 42px;
                }
            }
        }

        &:nth-of-type(9) {
            svg {
                width: 109px;
                height: 65px;
                flex-shrink: 0;

                @include below(768px) {
                    width: 94px;
                    height: 55px;
                }
            }
        }
    }

}