@import "main-module";

.personal {
    padding-bottom: 100px;

    @include below(768px) {
       padding-bottom: 80px;
    }

    &__title {
        font-size: 44px;
        font-weight: 700;
        margin-bottom: 20px;

        @include below(768px) {
            font-size: 32px;
        }
    }

    &__row {
        display: flex;
        align-items: start;

        &_second {
            @include below(1300px) {
                flex-direction: column;

                .personal__list {
                    margin-bottom: 20px;
                }
            }

        }

        @include below(992px) {
            flex-direction: column;
        }
    }

    &__list {
        display: flex;
        flex-direction: column;
        width: 100%;
        max-width: 306px;
        background-color: $color_white;
        padding: 20px;
        margin-right: 24px;
        border-radius: 20px;
        border: 1px solid #F7F9FB;
        box-shadow: 0px 10px 20px -10px rgba(0, 0, 0, 0.15);

        @include below(992px) {
           display: none;
        }
    }

    &__list-link {
        width: 100%;
        font-size: 16px;
        font-weight: 600;
        line-height: 20px;
        color: $color_black;
        padding: 12px 20px;
        border-radius: 10px;
        transition: color .3s linear, background-color .3s linear;

        &:not(:last-child) {
            margin-bottom: 10px;
        }

        &:hover,
        &:active {
            color: $color_blue;
            background-color: $color_light_blue;
        }

        &_active {
            color: $color_blue;
            background-color: $color_light_blue;
        }
    }

    &__list-btn {
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 12px 20px;
        margin-top: 60px;
        background-color: #F7F9FB;
        border-radius: 10px;
        font-size: 16px;
        font-weight: 600;
        line-height: 20px;

        svg {
            width: 20px;
            height: 20px;
        }
    }

    &__wrap {
        width: 100%;
        border-radius: 20px;
        border: 1px solid #F7F9FB;
        background-color: $color_white;
        box-shadow: 0px 10px 20px -10px rgba(0, 0, 0, 0.15);
        padding: 40px;

        @include below(768px) {
            padding: 20px 15px 15px 15px;
        }

        .personal__list-btn {
            display: none; 
            @include below(768px) {
                display: flex;
                margin-top: 40px;
            }
        }
    }

    &__wrap-title {
        font-size: 24px;
        font-weight: 700;
        line-height: 160%;
        margin-bottom: 20px;
    }

    &__wrap-subtitle {
        font-size: 18px;
        font-weight: 700;
        line-height: 160%;
        margin-bottom: 20px;
    }

    &__flex {
        display: flex;
        flex-wrap: wrap;
        margin: -20px -59px 60px;

        @include below(540px) {
            flex-direction: column;
            margin: -20px 0 20px;
        }
    }

    &__flex-item {
        display: flex;
        flex-direction: column;
        margin: 20px 59px 0;

        span {
            &:first-of-type {
                font-size: 12px;
                font-weight: 500;
                line-height: 160%;
                color: $color_grey;
            }

            &:last-of-type {
                font-size: 16px;
                font-weight: 500;
                line-height: 160%;
            }
        }

        @include below(540px) {
            margin: 20px 0 0;
        }
    }

    &__form {
        .form__title {
            font-size: 18px;
            font-weight: 700;
            line-height: 160%;
            margin-bottom: 20px;
        }

        .form__row {
            display: flex;
            flex-wrap: wrap;
            margin: 0 -5px;

            @include below(1240px) {
                margin: -10px -5px 0;
            }

            @include below(768px) {
                margin: -10px 0 0;
            }
        }

        .form__label {
            width: calc(25% - 10px);
            margin: 0 5px;

            @include below(1240px) {
                width: calc(50% - 10px);
                margin: 10px 5px 0;
            }

            @include below(768px) {
                width: 100%;
                margin: 10px 0 0;
            }
        }

        .form__btn {
            width: calc(25% - 10px);
            margin: 0 5px;

            @include below(1240px) {
                width: calc(50% - 10px);
                margin: 10px 5px 0;
            }

            @include below(768px) {
                width: 100%;
                margin: 10px 0 0;
            }
        }
    }

    &__accordion {
        padding: 20px;
        border-radius: 20px;
        border: 1px solid $color_blue;
        box-shadow: 0px 10px 20px -10px rgba(0, 0, 0, 0.15);

        &_second {
            margin: 20px 0 40px;
        }   
        @include below(768px) {
            padding: 20px 15px;
        }

    }

    &__head {
        display: flex;
        align-items: center;
        position: relative;

        &_active {
            margin-bottom: 20px;
            padding-bottom: 20px;
            border-bottom: 1px solid $color_grey_light;
            .personal__head-icon {
                svg {
                    transform: rotate(180deg);
                }
            }

        }

        @include below(768px) {
            flex-wrap: wrap;
        }
    }

    &__left {
        display: flex;
        align-items: center;
        flex: auto;

        @include below(768px) {
           order: 1;
        }

        &-icon {
            width: 44px;
            height: 44px;
            display: flex;
            align-items: center;
            justify-content: center;
            background-color: $color_blue;
            border-radius: 50%;
            margin-right: 20px;

            &_grey {
                background-color: $color_grey_light;
                color: $color_grey;
            }
            @include below(768px) {
                width: 24px;
                height: 24px;
                margin-right: 10px;
            }

            svg {
                width: 24px;
                height: 24px;

                @include below(768px) {
                    width: 14px;
                    height: 14px;
                }
            }
        }

        &-title {
            font-size: 18px;
            font-weight: 700;
        }
    }

    &__head-icon {
        margin-left: 30px;
        svg {
            width: 34px;
            height: 34px;
            transition: transform .3s linear;

            @include below(768px) {
                width: 24px;
                height: 24px;
            }
        }

        @include below(768px) {
            order: 2;
            margin-left: 0;
        }
    }

    &__head-text {
        display: block;
        font-size: 14px;
        font-weight: 500;
        span {
            color: $color_grey;
        }

        @include below(768px) {
            width: 100%;
            order: 3;
            margin-top: 10px;
        }
    }

    &__content {
        max-height: 0;
        opacity: 0;
        overflow: hidden;
        transition: max-height 0.3s linear, overflow 0.3s linear, opacity 0.3s linear;

        &-title {
            font-size: 14px;
            font-weight: 700;
            line-height: 140%;
            margin-bottom: 10px;
        }
    }

    &__content-row {
        display: flex;
        justify-content: space-between;
        margin-bottom: 40px;

        @include below(1260px) {
            flex-direction: column;
            justify-content: stretch;
            margin: -10px 0 40px;
        }

        @include below(768px) {
            margin: -10px 0 20px;
        }
    }

    &__row-item {
        display: flex;
        align-items: center;
        font-size: 14px;
        font-weight: 500;
        line-height: 140%;
        svg {
            width: 20px;
            height: 20px;
            margin-right: 6px;
        }

        @include below(1260px) {
            margin: 10px 0 0;
        }
    }

    &__content-bottom {
        display: flex;
        align-items: center;
        justify-content: space-between;

        @include below(540px) {
            flex-direction: column;
            align-items: start;
        }
    }

    &__content-btn {
        padding: 10px;

        @include below(540px) {
           width: 100%;
           margin-top: 20px;
        }
    }

    &__info {
        display: flex;
        padding: 20px;
        border-radius: 20px;
        background-color: #FFF6EB;
        margin-bottom: 40px;

        @include below(768px) {
            padding: 20px 15px 20px 20px;
        }

        @include below(540px) {
            margin: 0;
        }

        svg {
            width: 18px;
            height: 20px;
            flex-shrink: 0;
            margin-right: 10px;
        }

        &-text {
            font-size: 16px;
            font-weight: 600;
            color: #FFA234;

            a {
                font-weight: 800;
                color: #FFA234;
                text-decoration: underline;
            }
        }

    }

    &__btn {
        @include below(540px) {
            display: none;
        }
    }

    &__table-head {
        padding: 12px 0 13px;
        border-top: 1px solid $color_grey_light;
        border-bottom: 1px solid $color_grey_light;
        display: flex;
        align-items: center;
        margin-bottom: 22px;

        @include below(992px) {
            display: none;
        }
    }

    &__table-title {
        font-size: 14px;
        font-weight: 600;
        color: $color_grey;
      

        &:nth-of-type(1) {
            width: 19.5%;
            padding-left: 20px;
        }

        &:nth-of-type(2) {
            width: 20.5%;
        }

        &:nth-of-type(3) {
            width: 22.4%;

        }

        &:nth-of-type(4) {
            width: 27.6%;
        }

        &:nth-of-type(5) {
            padding-right: 40px;
        }
    }

    &__table {
        @include below(992px) {
            border-top: 1px solid $color_grey_light;
        }
    }

    &__table-row {
        display: flex;

        @include above(993px) {
            &:not(:last-child) {
                margin-bottom: 24px;
            }
        }

        @include below(992px) {
            flex-direction: column;
            padding: 20px 0;
            border-bottom: 1px solid $color_grey_light;
        }
    }

    &__table-item {
        .personal__table-title {
            display: none;
            padding: 0;

            @include below(992px) {
                display: block;
                white-space: nowrap;
            }
        }

        &:nth-of-type(1) {
            
            @include above(993px) {
                width: 19.5%;
                padding-left: 20px;
            }
        }

        &:nth-of-type(2) {
            @include above(993px) {
                width: 20.5%;
            }
        }

        &:nth-of-type(3) {
            
            @include above(993px) {
                width: 22.4%;
            }

        }

        &:nth-of-type(4) {
            @include above(993px) {
                width: 25.5%;
            }
        }

        @include below(992px) {
            display: flex;
            justify-content: space-between;

            &:not(:last-child) {
                margin-bottom: 10px;
            }
        }
    }

    &__table-text {
        display: flex;
        align-items: center;
        font-size: 14px;
        font-weight: 600;


        &_green {
            color: $color_green;
        }

        &_red {
            color: red;
        }

        svg {
            width: 20px;
            height: 20px;
            margin-right: 6px;
        }
    }
}