@import "main-module";

.header {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    z-index: 100;
    padding: 14px 0;
   
    &.header_second {
        position: static;
        background-color: #fff;
        box-shadow: 0px 10px 40px -10px rgba(0, 0, 0, 0.15);
    }

    &__row {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    &__logo {
        display: flex;
        align-items: center;
        font-size: 26px;
        font-weight: 800;
        color: $color_white;
        transition: color .3s linear;
        text-transform: uppercase;
        z-index: 501;

        .header.header_second & {
            color: $color_black;
        }

        .header.header_second.is-show & {
            @include below(556px) {
                color: $color_white;
            }
        }

        svg {
            width: 33px;
            height: 30px;
            flex-shrink: 0;
            margin-right: 5px;
        }
    }

    &__nav {
        .header__menu & {
            @include below(1200px) {
                display: block;
            }
        }
        @include below(1200px) {
            display: none;
        }
    }

    &__nav-list {
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        margin: 0 -15px;

        .header__menu & {
            @include below(1200px) {
                flex-direction: column;
                margin: -40px 0 0;
            }
        }
    }

    &__nav-item {
        margin: 0 15px;

        .header__menu & {
            @include below(1200px) {
                flex-direction: column;
                margin: 40px 0 0;
            }
        }
    }

    &__nav-link {
        font-size: 14px;
        font-weight: 600;
        color: $color_white;
        transition: color .3s linear;
        color: $color_white;
        transition: text-decoration .3s linear;
        
        .header.header_second & {
            color: $color_black;
        }

        &:hover:not(:active) {
            color: $color_blue;
        }

        .header__menu & {
            @include below(1200px) {
               font-size: 22px;
            }
        }
    }

    &__item {
        display: flex;
        align-items: center;
    }

    &__btn {
        font-weight: 600;
        padding: 8px 10px;
        margin-right: 10px;

        &:hover {
            border-color: transparent;
        }

        svg {
            margin-right: 4px;
        }

        @include below(540px) {
            margin-right: 20px;
            span {
                display: none;
            }

            svg {
                margin-right: 0;
            }
        }

        @include below(400px) {
            padding: 8px 10px;
        }
    }

    &__select {
        select {
            @include below(1200px) {
                display: none;
            }
        }
        .select2.select2-container {
            .select2-selection--single {
                width: 56px;
                padding: 8px 10px;
                height: 32px;
                background-color: #F7F9FB;
                border-color: transparent;
            }

            @include below(1200px) {
                display: none;
            }
          
        }
    }

    &__burger {
        position: relative;
        z-index: 501;
        display: none;
        color: $color_white;
        .header.header_second & {
            color: $color_black;
        }

        @include below(1200px) {
            display: block;
        }

        &:not(.is-active) {
            [data-type="close"] {
                display: none;
            }
        }

        &.is-active {
            [data-type="burger"] {
                display: none
            }
        }

        svg {
            width: 32px;
            height: 32px;
        }
    }

    &__menu {
        position: fixed;
        top: 0;
        right: 0;
        bottom: 0;
        z-index: 500;
        width: 100%;
        max-width: 400px;
        padding: 50px 15px;
        background-image: url("../img/content/menu_banner.png");
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center;
        transform: translateX(100%);
        visibility: hidden;
        overflow-y: auto;
        display: none;
        align-items: center;
        transition: all .3s linear;

        .header__nav .header__nav-link {
            color: $color_white;
        }

        @include below(1200px) {
            display: flex;
            flex-direction: column;
        }

        @include below(555px) {
            max-width: none;
        }

        &.is-show {
            visibility: visible;
            transform: none;
        }

        .header__btn {
            margin: 55px 0 40px;
            padding: 14px 30px;

            span {
                display: block;
                margin-left: 4px;
            }

            @include below(479px) {
                width: 100%;
                margin: 55px 0 86px;
            }
        }

        .header__select {
            & ~ .select2.select2-container {
                
                @include below(1200px) {
                    display: block;
                    margin-top: 40px;
                }

                @include below(479px) {
                    margin-top: 86px;
                }
            }
        }
        .select2-container--default .select2-selection--single {
            background-color: #F7F9FB;
        }
    }
}