@import "main-module";

.breadcrumbs {
    &__list {
        display: flex;
        align-items: center;
        padding: 20px 0 10px;
    }

    &__item {
        font-size: 12px;
        font-weight: 400;
        line-height: normal;
        
        &:not(:last-child) {
            &::after {
                content: "/";
                display: inline-block;
                margin-right: 4px;
                color: #000;
            }
        }

        &:nth-last-of-type(2) {
            &::after {
                color: #808191;
            }
        }

        a {
            color: #000;
        }

        span {
            color: #808191;
        }
    }
}