@import "main-module";
.fancybox__container {
    .fancybox__backdrop {
        background-color: rgba(0, 0, 0, 0.20);
        backdrop-filter: blur(3px);
    }
}
.modal {
    max-width: 368px;
    &__wrap {
        box-shadow: 0px 10px 20px -10px rgba(0, 0, 0, 0.15);
        border: 1px solid #F7F9FB;
        background-color: #fff;
        border-radius: 20px;
        padding: 40px;
        position: relative;

        @include below(768px) {
            padding: 40px 10px;
        }
    }

    &__close {
        position: absolute;
        top: 10px;
        right: 10px;
        svg {
            width: 34px;
            height: 34px;
        }
    }

    &__icon {
        width: 84px;
        height: 84px;
        margin: 0 auto 10px;
        svg {
            width: 84px;
            height: 84px;
        }
    }

    &__text {
        text-align: center;

        font-family: Raleway;
        font-size: 16px;
        font-weight: 500;
        line-height: 160%;
    }

    &__btn {
        margin-top: 20px;
        display: flex;
        justify-content: center;

        @include below(500px) {
            .btn {
                width: 100%;
            }
        }
    }
}