@import "main-module";

.intro {
    overflow: hidden;
    position: relative;
    padding: 120px 0 60px;

    @include below(768px) {
        padding: 112px 0 50px;
    }

    &__banner {
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        z-index: 0;
        img {
            width: 100%;
            height: 100%;
        }
    }

    &__img {
        position: absolute;
        z-index: 1;

        &_first {
            top: 0;
            left: 0;
            @include below(1100px) {
                width: 400px;
            }

            @include below(768px) {
                width: 300px;
            }

            @include below(500px) {
                width: fit-content;
            }
        }
        &_second {
            top: 0;
            right: 0;
            @include below(1100px) {
                width: 400px;
            }

            @include below(768px) {
                width: 300px;
            }

            @include below(500px) {
                width: fit-content;
            }
        }

        &_stingray {
            top: 25%;
            right: 0;

            @include below(1200px) {
                width: 300px;
                height: 300px;
                right: -80px;
            }

            @include below(768px) {
                width: 200px;
                right: -40px;
                top: 20%;
            }

            @include below(500px) {
                width: fit-content;
                height: fit-content;
                top: 30%;
                right: 0;
            }
        }
    }

    &__content {
        width: 100%;
        max-width: 800px;
        margin: 0 auto 60px;
        display: flex;
        flex-direction: column;
        align-items: center;
        text-align: center;
        color: $color_white;
        position: relative;
        z-index: 2;
        @include below(479px) {
           align-items: stretch;
        }
    }

    &__title {
        font-size: 74px;
        font-weight: 800;
        line-height: 100%;
        margin-bottom: 20px;

        @include below(1240px) {
            font-size: 55px;
        }

        @include below(1024px) {
            font-size: 45px;
        }

        @include below(768px) {
            font-size: 32px;
        }
    }

    &__subtitle {
        font-size: 16px;
        font-weight: 500;
        line-height: 140%;
        margin-bottom: 60px;
        br {
            display: none;

            @include below(479px) {
                display: block;
            }
        }
        @include below(768px) {
           margin-bottom: 40px;
        }
    }

    &__buttons {
        display: flex;

        @include below(479px) {
            flex-direction: column;
        }
    }

    &__btn {
        @include below(479px) {
           width: 100%;
        }
        &:nth-of-type(1) {
            margin-right: 24px;
            &:hover {
                border-color: transparent;
            }

            @include below(479px) {
                margin: 0 0 20px;
            }
        }

        &:nth-child(2) {
            color: $color_white;
            border: 1px solid $color_white;
        }
    }

    &__row {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 0 150px;
        position: relative;
        z-index: 2;
        color: $color_white;

        @include below(1200px) {
            padding: 0 0 0 220px;
        }

        @include below(992px) {
            padding: 0 0 0 110px;
        }

        @include below(768px) {
            padding: 0 0 0 40px;
        }

        @include below(479px) {
            padding: 0;
        }

    }

    &__col {
        flex: auto;
        display: flex;
        justify-content: space-between;
        align-items: center;

        &:first-of-type {
            margin-right: 100px;

            @include below(1200px) {
                margin-right: 46px;
            }

            @include below(390px) {
                margin-right: 25px;
            }

        }

        @include below(1200px) {
            flex-direction: column;
            align-items: start;

            .intro__item {
                &:first-of-type {
                    margin-bottom: 30px;
                }
            }
        }
        
    }

    &__item {
       display: flex;
       flex-direction: column;
       align-items: start;
    }

    &__numb {
        white-space: nowrap;
        font-size: 54px;
        font-weight: 800;
        line-height: 100%;
        margin-bottom: 10px;

        @include below(1240px) {
           font-size: 42px;
           margin-bottom: 4px;
        }
    }

    &__text {
        font-size: 14px;
        font-weight: 500;
        line-height: 140%; 
        $color_grey_light: #DBDEE7;
    }
}