@import "main-module";

.filter {
    margin-top: 200px;

    &__wrapper {
        width: 100%;
        max-width: 400px;
        box-shadow: 0px -10px 40px -10px rgba(0, 0, 0, 0.15);
        padding: 40px 20px;

        @include below(768px) {
            padding: 40px 0;
        }
    }

    &__head {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 40px;
        padding: 0 20px;
    }

    &__title {
        font-family: Raleway;
        font-size: 24px;
        font-style: normal;
        font-weight: 700;
        line-height: normal;
    }

    &__close {
        width: 44px;
        height: 44px;

        svg {
            width: 44px;
            height: 44px;
        }
    }
    &__block {
        &:not(:last-of-type) {
            margin-bottom: 36px;
        }
    }

    &__block-label {
        font-family: Raleway;
        font-size: 16px;
        font-weight: 700;
        line-height: normal;
        margin-bottom: 16px;
    }

    &__block-row {
        display: flex;
        align-items: center;
    }

    &__block-input {
        &:not(:last-child) {
            margin-right: 20px;
        }
        .input {
            width: 100%;  
        }
    }

    &__block-select {
        select,
        .select2-container {
            width: 100%;
        }
    }

    &__button {
        display: flex;
        align-items: center;
        margin-top: 20px;
        margin: 0 20px;
        .btn {
            &:not(:last-child) {
                margin-right: 20px;
            }
            flex: auto;
        }
    }

    &__accordion-block {
        padding: 16px 0;
        border-bottom: 1px solid #F7F9FB;
        
        &:first-child {
            border-top: 1px solid #F7F9FB;
        }
    }

    
    &__scrolling,
    &__accordion-scrolling {
        overflow-x: hidden;
        overflow-y: auto;
        max-height: 400px;

        &::-webkit-scrollbar {
            width: 5px;
        }
        
        /* Track */
        &::-webkit-scrollbar-track {
            background: #f1f1f1;
        }
        
        /* Handle */
        &::-webkit-scrollbar-thumb {
            background: #416FF4;
        }
        
        /* Handle on hover */
        &::-webkit-scrollbar-thumb:hover {
            background: #416FF4;
        }
    }

    &__scrolling {
        max-height: 700px;
        padding: 0 20px;
    }

    &__accordion-head {
        display: flex;
        align-items: center;
        justify-content: space-between;
        cursor: pointer;
        padding: 5px 0;
    }

    &__accordion-title {
        font-family: Raleway;
        font-size: 16px;
        font-weight: 700;
        line-height: normal;
        color: #000;
        margin-right: 12px;
    }

    &__accordion-icon {
        width: 24px;
        height: 24px;

        .filter__accordion-head.is-active & {
            svg {
                transform: rotate(180deg);
            }
        }

        svg {
            width: 24px;
            height: 24px;
            transition: transform .3s linear;
        }
    }

    &__accordion-body {
        display: none;
    }

    &__accordion-content {
        padding: 16px 0;
    }

    &__accordion-checkbox {
        &:not(:last-child) {
            margin-bottom: 10px;
        }
    }

    &__accordion-input {
        position: relative;
        margin-bottom: 16px; 
        display: block;

        .input {
            width: 100%;
        }

        .icon {
            position: absolute;
            top: 50%;
            right: 14px;
            z-index: 10;
            transform: translateY(-50%);
        
            width: 16px;
            height: 16px;
        }
    }
}