@import "main-module";

.input {
    padding: 14px 20px;
    border-radius: 10px;
    border: 1px solid #DBDEE7;

    font-family: Raleway;
    font-size: 14px;
    font-weight: 600;
    line-height: normal;

    color: #000;

    &:disabled {
        background-color: #F7F9FB;
        border-color: #F7F9FB;
        color: #808191;
    }
}