// @import "main-module";

:root {
    --bs-gutter-x: 1.5rem;
    --bs-gutter-y: 0;
    
}

html {
    font-feature-settings: 'ordn' on;
    scroll-behavior: smooth;
    scroll-padding: 40px;
}

*, *::after, *::before {
    box-sizing: border-box;
    font-kerning: none;
    font-feature-settings: 'ordn' on;
    font-variant-numeric: lining-nums proportional-nums;
    box-sizing: border-box;
    -webkit-tap-highlight-color: transparent;
    -webkit-text-size-adjust: 100%;
}

h1, h2, h3, h4, h5, h6, ul, p {
    margin: 0;
    padding: 0;
}

body {
    font-family: $accentFont;
    font-size: 16px;
    line-height: normal;
    font-variant-numeric: lining-nums proportional-nums;
}

button {
    cursor: pointer;
    padding: 0;
    border: 0;
}

a {
    cursor: pointer;
    text-decoration: none;
  
}

.main {
    min-height: 80vh;
}

.container {
    width: 100%;
    margin: 0 auto;
    
    @include below(1340px)  {
        padding-right: calc(var(--bs-gutter-x) * 1);
        padding-left: calc(var(--bs-gutter-x) * 1);
    }
    
    @include above(992px) {
        max-width: 1296px;
    }

    @include above(768px) {
        max-width: 720px;
    }
    
    @include above(576px) {
        max-width: 540px;
    }
}

ul {
  list-style-type: none;
}

:focus {
    outline-style: none;
    outline-width: 0 !important;
    outline-color: none !important;
}



input,
button,
textarea,
fieldset {
  background: transparent;
  border: 0;
  padding: 0;
}

img {
    max-width: 100%;
    height: auto;
}

.main {
    &_bg {
        background-color: #F7F9FB;
    }
}

