@import "main-module";

.article {
    background-color: #F7F9FB;
    padding: 60px 0;

    @include below(768px) {
        padding: 60px 0 40px;
    }

    &__head {
        text-align: center;
        margin-bottom: 40px;
    }

    &__title {
        font-size: 44px;
        font-weight: 700;
        margin-bottom: 20px;

        @include below(1024px) {
            font-size: 32px;
        }
    }

    &__text {
        font-size: 16px;
        font-weight: 500;
        line-height: 160%;
    }

    &__row {
        &.swiper {
            margin: -30px 0;
            padding: 30px 0;
        }
        .swiper-wrapper {
            display: flex;
            align-items: stretch;
        }

        .swiper-pagination {
            display: none;
            @include below(768px) {
                display: block;
            }
            &.swiper-pagination-horizontal {
                bottom: 0;
            }
            .swiper-pagination-bullet {
                width: 8px;
                height: 8px;
                margin: 0 5px;
                background-color: rgba(0, 0, 0, 0.20);
                opacity: 1;
                transition: transform .3s linear;
            }

            .swiper-pagination-bullet-active {
                transform: scale(1.3);
                background-color: $color_blue;
            }
        }
    }

    &__item {
        &.swiper-slide {
            display: flex;
            flex-direction: column;
            height: initial;
        }
    }

    &__btn {
        white-space: nowrap;
        width: 100%;
        max-width: 135px;
        margin: 40px auto 0;

        @include below(479px) {
            margin: 50px auto 0;
            max-width: none;
        }
    }
}