@import "main-module";


.calculator-mobile {
    margin-top: 100px;
    background-color: #fff;
    padding: 40px 15px;
    box-shadow: 0px 10px 20px -10px rgba(0, 0, 0, 0.15);
    border: 1px solid #F7F9FB;
    &__wrapper {
        // max-width: 375px;
    }

    &__head {
        display: flex;
        justify-content: space-between;
        margin-bottom: 20px;

        &.calculator-mobile__head_second {
            display: block;
        }
    }

    &__head-wrap {
        
    }

    &__head-title {
        font-family: Raleway;
        font-size: 24px;
        font-weight: 700;
        line-height: normal;
        color: #000;
        margin-bottom: 10px;
        padding-right: 10px;
    }

    &__head-text {
        font-family: Raleway;
        font-size: 14px;
        font-weight: 400;
        line-height: normal;
        color: #808191;
    }

    &__head-select {
        display: flex;
        align-items: center;
        margin: 0 -5px;

        .select2-container {
            width: calc(50% - 10px);
            margin: 0 5px;
        }
        &:not(:last-child) {
            margin-bottom: 10px;
        }
        strong {
            font-family: Raleway;
            font-size: 14px;
            font-weight: 600;
            line-height: normal;
            margin-right: 6px;
            width: calc(50% - 10px);
            margin: 0 5px;
        }
    }

    &__close {
        min-width: 44px;
        height: 44px;

        svg {
            width: 44px;
            height: 44px;
        }
    }

    &__block {
        padding-top: 20px;
        margin-bottom: 20px;
        &:not(:last-child) {
            border-top: 1px solid #DBDEE7;
        }
    }

    &__block-row {
        display: flex;
        align-items: center;
        margin: 0 -5px;
        &:not(:last-child) {
            padding-bottom: 20px;
        }  
    }

    &__block-item {
        width: calc(50% - 10px);
        margin: 0 5px; 

        .calculator-mobile__input {
            width: auto;
        }
    }

    &__block-head {
        display: flex;
        align-items: center;
        font-family: Raleway;
        line-height: normal;

        &.is-right {
            justify-content: flex-end;
        }

        &.is-sum {
            flex-direction: column;
            align-items: flex-end;

            strong {
                font-size: 24px;
                font-weight: 700;
            }
        }

        span {
            font-size: 12px;
            font-weight: 500;
            color: #808191;
            margin-right: 10px;
        }

        strong {
            font-size: 14px;
            font-weight: 600;
        }
    }

    &__inputs {
        &:not(:last-child) {
            padding-bottom: 20px;
        }  
        label {
            font-family: Raleway;
            font-size: 12px;
            font-weight: 500;
            line-height: normal;
            display: block;
            margin-bottom: 10px;
            color: #808191;
        } 
    }
    
    &__inputs-row {
        display: flex;
        align-items: center;
        margin: 0 -5px;
    }

    &__select,
    &__input {
        width: calc(50% - 10px);
        margin: 0 5px; 
        .input {
            width: 100%;
        }
    }

    &__select {
        .select2-container {
            width: 100%;
        }
    }

    &__link {
        display: flex;
        align-items: center;
        font-family: Raleway;
        font-size: 14px;
        font-weight: 600;
        line-height: normal;
        color: #416FF4;
        svg {
            width: 18px;
            height: 18px;
            margin-left: 4px;
        }
    }

    &__copy {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        svg {
            width: 16px;
            height: 16px;
            margin-right: 10px;
        }
    }

    &__control {
        display: flex;
        align-items: center;
    }

    &__radio {
        &:not(:last-child) {
            margin-right: 20px;
        }
        .checkbox__wrap,
        .radio-button__wrap {
            display: flex;
            align-items: center;
        }

        .checkbox__text,
        .radio-button__text {
            margin-left: 0;
            margin-right: 10px;
        }
    }

    &__bottom-flex {
        display: flex;
        align-items: center;
        margin: 20px 0;

        .calculator-mobile__input {
            margin: 0 0 0 10px;
            max-width: 80px;
        }
    }
}