@import "main-module";

.earn {
    padding: 80px 0;

    @include below(768px) {
        padding: 60px 0;
    }

    &__content {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;

        @include below(479px) {
            align-items: stretch;
        }
    }

    &__title {
        text-align: center;
        font-size: 44px;
        font-weight: 700;
        margin-bottom: 40px;

        @include below(1024px) {
            font-size: 32px;
        }
    }

    &__form {

        .form__row {
            @include below(479px) {
                flex-direction: column;
            }
        }
        .form__label {
            margin-right: 20px;
            @include below(479px) {
                width: 100%;
                margin: 0 0 20px;
            }
        }
        .form__input {
            border: none;
            background-color: #F7F9FB;
            &::placeholder {
                color: $color_grey;
            }
        }

        .form__btn {
            @include below(479px) {
                width: 100%;
            }
        }
    }
}