@import "main-module";

.faq {
    &__title {
        @include title;
        margin-bottom: 40px;

        @include below(1024px) {
            margin-bottom: 20px;
        }
    }

    &__wrap {
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        margin-bottom: 100px;

        @include below(1024px) {
            display: block;
        }
    }

    &__menu {
        border-radius: 10px;
        box-shadow: 0px 10px 20px -10px rgba(0, 0, 0, 0.15);
        border: 1px solid #DBDEE7;
        background-color: #fff;
        padding: 20px;
        width: 23.61%;
        position: sticky;
        top: 20px;

        @include below(1200px) {
            width: 30%;
        }

        @include below(1024px) {
            position: static;
            width: 100%;
            margin-bottom: 40px;
        }
    }
    
    &__menu-item {
        &:not(:last-child) {
            margin-bottom: 20px;
        }
        &.is-active a {
            font-weight: 700;
            color: #416FF4;
        }
        a {
            font-family: Raleway;
            font-size: 14px;
            font-weight: 600;
            line-height: 140%;
            color: #000;
        }
    }

    &__block {
        width: 66%;

        @include below(1024px) {
            width: 100%;
        }
    }

    &__block-title {
        font-family: Raleway;
        font-size: 24px;
        font-weight: 700;
        line-height: normal;
        margin-bottom: 40px;
        @include below(1024px) {
            margin-bottom: 20px;
        }
        &:not(:first-child) {
            padding-top: 80px;
            @include below(1024px) {
                margin-bottom: 40 px;
            }
        }
    }

    &__content {
        h3 {
            font-family: Raleway;
            font-size: 18px;
            font-weight: 700;
            line-height: normal;
            margin-bottom: 10px;
            &:not(:first-child) {
                padding-top: 20px;
            }
        }
        p {
            font-family: Raleway;
            font-size: 16px;
            font-weight: 500;
            line-height: 160%;
            margin-bottom: 10px;
            svg {
                margin: 0 7px -10px 7px;
            }
        }

        ul, ol {
            font-family: Raleway;
            font-size: 16px;
            font-weight: 500;
            line-height: 160%;
            padding-left: 15px;
            margin: 0;
        }
        img, 
        picture {
            display: block;
            margin-bottom: 20px;
        }

        table {
            width: 100%;

            margin: 0;
            padding: 0;

            font-family: Raleway;
            font-size: 16px;
            line-height: 160%;

            thead {
                @include below(768px) {
                    display: none;
                }
            }

            thead th {
                width: 33.33333%;
                padding: 10px 20px;
                background-color: #dbdee7;
                text-align: left;
            }

            tbody tr {
                @include below(768px) {
                    display: block;
                    width: 100%;
                }
                &:nth-child(even) {
                    td {
                        background-color: rgba(219, 222, 231, .2);
                    }   
                }
            }

            tbody td {
                width: 33.33333%;
                padding: 10px 20px;
                font-weight: 500;

                @include below(768px) {
                    display: block;
                    width: 100%;
                    padding: 10px 15px;

                    &:not(:last-child) {
                        padding-bottom: 14px;
                    }
                }

                span {
                    display: block;
                    font-size: 12px;
                    font-weight: 400;
                    line-height: normal;
                    color: #808191;
                    margin-bottom: 4px;
                }
            }
        }
    }
}