@import "main-module";

.switch-radio {
    &__wrap {
        cursor: pointer;
        display: inline-flex;
        align-items: center;

        input {
            position: absolute;
            left: -10000%;

            &:checked ~ .switch-radio__slider {
                border: 1px solid #416FF4;
                &::before {
                    transform: translate(82%, -50%);
                    background-color: #416FF4;
                }
            }
        }
    }

    &__slider {
        display: block;
        width: 32px;
        min-width: 32px;
        height: 20px;
        border: 1px solid #808191;
        border-radius: 50px;
        position: relative;
        transition: border-color .3s linear;
        &::before {
            content: "";
            position: absolute;
            width: 16px;
            height: 16px;
            border-radius: 50%;
            background-color: #808191;
            top: 50%;
            transform: translate(1px, -50%);
            transition: background-color .3s linear, transform .3s linear;
        }
    }

    &__text {
        font-size: 14px;
        font-weight: 600;
        line-height: 110%;
        color: #000;
        margin-left: 6px;

        @include below(1024px) {
            font-size: 12px;
        }
    }
}