@import "main-module";

.checkbox {
    &__wrap {
        display: inline-flex;
        align-items: center;
        cursor: pointer;
        input {
            position: absolute;
            top: 0;
            left: -1000%;

            &:checked ~ .checkbox__mark {
                background-color: #416FF4;
                border-color: #416FF4;
                color: #fff;
            }

            &:disabled {
                & ~ .checkbox__mark {
                    background-color: #F7F9FB;
                    border-color: #F7F9FB;
                }

                & ~ .checkbox__text {
                    color: #808191;
                }
            }
        }
    }

    &__mark {
        display: block;
        width: 20px;
        height: 20px;
        border: 1px solid #DBDEE7;
        border-radius: 6px;
        color: transparent;
        transition: background-color .3s linear, color .3s linear, border-color .3s linear;
        svg {
            width: 18px;
            height: 18px;
        }
    }

    &__text {
        font-family: Raleway;
        font-size: 14px;
        font-weight: 600;
        line-height: normal;
        color: #000;
        margin-left: 10px;

        &_second {
            margin-left: 5px;
        }

        a {
            color: $color_blue;
            text-decoration: underline;
            transition: text-decoration .3s liner;

            &:hover {
                text-decoration: none;
            }
        }
    }
}