@import "main-module";

.arbitrage {
    &__container {
        margin-top: 100px;
        margin-bottom: 100px;
    }
    &__title {
        @include title;
        margin-bottom: 40px;
        text-align: center;
    }

    &__board {
        
    }

    &__board-head {
        display: flex;
        position: relative;
        z-index: 100;

        @include below(768px) {
            display: none;
        }
    }

    &__tabs {
        margin-bottom: 20px;
        @include above(769px) {
            display: none;
        }
    }

    &__tab {
        width: 50%;
        padding: 40px 40px 28px;
        display: flex;
        justify-content: space-between;
        cursor: pointer;
        box-shadow: inset 0px -10px 20px -20px rgba(0, 0, 0, 0.15);
        border-radius: 0 0 0 20px;
        &:nth-of-type(1) {
            &:not(.arbitrage__tab_active) {
                border-radius: 0 0 20px 0;

                @include below(1024px) {
                    border-radius: 0;
                }
            }
        }

        &.arbitrage__tab_active {
            position: relative;
            border-radius: 20px 20px 0 0;
            box-shadow: 0px 0 20px -10px rgba(0, 0, 0, 0.15);
            &::after {
                content: "";
                position: absolute;
                bottom: 0;
                left: 50%;
                transform: translateX(-50%);
                width: 88%;
                height: 1px;
                background-color: #DBDEE7;
            }
        }


        &.arbitrage__tab_mob {
            @include above(769px) {
                display: none;
            }
        }

        @include below(768px) {
            width: 100%;
            padding: 27px 0 20px;
            margin-bottom: 25px;
            box-shadow: none;
            border-radius: 0;
            border-bottom: 1px solid #DBDEE7;
        }
    }

    &__tab-title {
        font-size: 24px;
        font-weight: 700;
        line-height: normal;
        margin-bottom: 10px;
        color: #000;

        .arbitrage__tab_active & {
            color: #416FF4;
        }

        @include below(768px) {
            color: #416FF4;
        }
    }

    &__tab-icon {
        margin-left: 24px;
        svg {
            width: 60px;
            height: 60px;

            @include below(768px) {
                width: 40px;
                height: 40px;
            }
        }
    }

    &__tab-text {
        font-size: 14px;
        font-weight: 500;
        line-height: 160%;
        color: #808191;
    }

    &__rates {
        width: 100%;
        max-width: 596px;
        padding: 20px 30px;
        border: 1px solid #DBDEE7;
        background-color: #fff;
        border-radius: 20px;
        position: relative;
        @include below(768px) {
            max-width: 48.5%; 
            padding: 20px;
            
        } 

        &:first-child {
            @include above(769px) {
                margin-bottom: 10px;
            }
        }

        &.arbitrage__rates_second {
            background-color: #416FF4;
            border-color: #416FF4;
        }
    }
    
    &__rates-figure {
        position: absolute;
        bottom: 0;
        right: 0;

        @include below(768px) {
            display: none;
        }
    }

    &__rates-title {
        font-size: 18px;
        font-weight: 700;
        line-height: 160%;
        color: #000;
        margin-bottom: 10px;

        .arbitrage__rates_second & {
            color: #fff;
        }

        @include below(768px) {
            text-align: center;
            margin-bottom: 20px;
        }
    }

    &__rates-list {
        display: flex;
        align-items: center;
        justify-content: space-between;

        @include below(768px) {
            flex-direction: column;
        }
    }

    &__rates-item {
        position: relative;
        display: flex;
        flex-direction: column;
        align-items: center;
        &:not(:last-child) {
            margin-right: 9.7%;

            @include below(768px) {
                margin-right: 0;
                margin-bottom: 10px;
            }

            &::after {
                content: "";
                background-repeat: no-repeat;
                width: 120%;
                height: 12px;
                position: absolute;
                bottom: 5px;
                right: -120%;

                @include below(768px) {
                    position: static;
                    height: 16px;
                    width: 12px;
                    margin-top: 10px;
                }

                .arbitrage__rates_second & {
                    background-image: url(../img/sprites/icon-arrow-3.svg);

                    @include below(768px) {
                        background-image: url(../img/sprites/icon-arrow-3.2.svg);
                    }
                }
            }
        }

        &_up {
            span {
                color: #22C55E;
            }

            &::after {
                background-image: url(../img/sprites/icon-arrow-1.svg);
                @include below(768px) {
                    background-image: url(../img/sprites/icon-arrow-1.2.svg);
                }
            }
        }

        &_down {
            span {
                color: #DE3618;
            }

            &::after {
                background-image: url(../img/sprites/icon-arrow-2.svg);
                @include below(768px) {
                    background-image: url(../img/sprites/icon-arrow-2.2.svg);
                }
            }
        }

        span {
            font-size: 16px;
            font-weight: 700;
            line-height: 140%;
            margin-bottom: 10px;

            .arbitrage__rates_second & {
                color: #fff;
            }
        }

        svg {
            width: 24px;
            height: 24px;
        }

        
    }

    &__rates-btn {
        display: block;
        width: 100%;
        border-color: #fff;
        color: #fff;
        margin-top: 20px;
        &:hover:not(:active) {
            color: #416FF4;
            background-color: #fff;
        }
        @include below(768px) {
            display: none;
        }
    }

    &__content {
        padding: 40px;
        border-radius: 20px;
        box-shadow: 0px 10px 20px -10px rgba(0, 0, 0, 0.15);
        position: relative;
        z-index: 1;
        &.arbitrage__content_left {
            border-radius: 0 20px 20px 20px;
        }
        &.arbitrage__content_right {
            border-radius: 20px 0 20px 20px;
        }
        @include below(1024px) {
            padding: 20px 15px;
        }
    }

    &__content-block {
        @include above(1201px) {
            display: flex;
            justify-content: space-between;    
        }
    }

    &__content-item {
        @include above(1201px) {
            width: 50%;
        }
        &_first {
            @include above(1201px) {
                width: 38%;
                margin-right: 24px;
            }

            @include below(1200px) {
                margin-bottom: 20px;
            }
        }

        &_second {
            @include below(768px) {
                display: flex;
                justify-content: space-between;
            } 
        }
    }

    &__content-title {
        font-size: 24px;
        font-weight: 700;
        line-height: normal;
        color: #000;
        margin-bottom: 20px;

        @include below(1024px) {
            font-size: 20px;
            margin-bottom: 10px;
        }
    }

    &__content-text {
        font-size: 16px;
        font-weight: 500;
        line-height: 160%;
        color: #808191;
        @include below(1024px) {
            font-size: 14px;
        }
        &:not(:last-of-type) {
            margin-bottom: 40px;

            @include below(1024px) {
                margin-bottom: 20px;
            }
        }
    }
}