@import "main-module";

.article-block {
    padding-bottom: 100px;

    @include below(768px) {
        padding-bottom: 80px;
    }
    &.article-block_second {
        padding-bottom: 0;
    }


    &__title {
        font-size: 44px;
        font-style: normal;
        font-weight: 700;
        margin-bottom: 20px;

        @include below(1024px) {
            font-size: 32px;
        }
    }

    &__tabs {
        margin-bottom: 20px;

        &_second {
            margin-bottom: 0;
        }
    }

    &__tabs-list {
        display: flex;
        align-items: center;
        margin: 0 -5px;

        @include below(768px) {
            overflow-x: auto;
            overflow-y: hidden;
            padding-bottom: 5px;
            margin: 0 -5px;
        }
    }

    &__tabs-item {
        margin: 0 5px;
    }

    &__tabs-button {
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 10px;
        background-color: #F7F9FB;
        border: 1px solid transparent;
        padding: 8px 10px;
        font-size: 14px;
        font-weight: 600;
        color: $color_black;
        transition: color .3s linear, background-color .3s linear, border-color .3s linear;

        svg {
            width: 16px;
            height: 16px;
            margin-left: 2px;
        }

        &:hover,
        &:active {
            background-color: $color_light_blue;
            border-color: $color_blue;
            color: $color_blue;
        }

        &_active {
            background-color: $color_light_blue;
            border-color: $color_blue;
            color: $color_blue;

            svg {
                fill: #416FF4;
            }
        }
    }

    &__info {
        display: flex;
        align-items: center;
        margin: 0 -10px 10px;

        @include below(455px) {
            margin: 0 -5px 10px;
        }
    }

    &__info-text {
        position: relative;
        margin: 0 10px;
        font-size: 14px;
        font-weight: 500;
        line-height: 140%;
        color: $color_grey;

        @include below(455px) {
            font-size: 11px;
            margin: 0 5px;
        }

        &:not(:last-child) {
            &::after {
                content: "";
                display: inline-block;
                position: absolute;
                top: 8px;
                right: -11px;
                width: 3px;
                height: 3px;
                background-color: $color_grey;
                border-radius: 50%;

                @include below(455px) {
                    top: 5px;
                    right: -6px;    
                }
            }
        }
    }

    &__inner {
        display: flex;
        align-items: start;
        position: relative;
        padding: 40px 0 60px;

        @include below(992px) {
            flex-direction: column-reverse;
        }

        @include below(768px) {
           padding: 40px 0 80px;
        }
    }

    &__main {
        width: 100%;
        max-width: 856px;
        margin-right: 134px;

        @include below(1240px) {
            margin-right: 60px;
        }

        @include below(992px) {
            margin: 60px 0 0;
        }

        h2 {
            font-size: 24px;
            font-weight: 700;
            line-height: 160%;
            margin-bottom: 20px;
        }

        h3 {
            font-size: 18px;
            font-weight: 700;
            line-height: 160%;

            margin-bottom: 10px;

            &:not(:first-of-type) {
                margin: 40px 0 10px;
            }
        }

        p {
            font-size: 16px;
            font-weight: 500;
            line-height: 160%;
        }
    }

    &__main-img {
        margin: 20px 0;
    }

    &__content {
        position: sticky;
        top: 40px;
        right: 0;
        width: 100%;
        max-width: 306px;
        padding: 20px;
        border-radius: 10px;
        border: 1px solid $color_grey_light;
        background-color: $color_white;
        box-shadow: 0px 10px 20px -10px rgba(0, 0, 0, 0.15);

        @include below(992px) {
            position: static;
        }
    }

    &__content-title {
        font-size: 20px;
        font-weight: 700;
        line-height: 120%;
        margin-bottom: 20px;
    }

    &__content-item {
        &:not(:last-of-type) {
            margin-bottom: 20px;
        }
    }

    &__content-link {
        display: inline-block;
        color: $color_black;
        font-size: 14px;
        font-weight: 500;
        line-height: 140%;
        transition: color .3s linear, font-weight .3s linear;

        &:hover,
        &:active {
            font-weight: 700;
            color: $color_blue;
        }

        &_active {
            font-weight: 700;
            color: $color_blue;
        }
    }

    &__wrap {
        display: flex;
        flex-wrap: wrap;
        align-items: stretch;
        margin: -20px -12px 0;

        @include below(540px) {
            margin: -20px 0 0;
        }
    }

    &__item {
        width: calc(25% - 24px);
        margin: 20px 12px 0;

        @include below(1240px) {
            width: calc(33.333% - 24px);
        }

        @include below(992px) {
            width: calc(50% - 24px);
        }

        @include below(540px) {
            width: 100%;
            margin: 20px 0 0;
        }

        &_second {
            .card__body {
                .card__body-top {
                    @include below_and_above(768px,541px) {
                       flex-direction: column;
                       align-items: start;
                        .card__data {
                            margin-bottom: 5px;
                        }
                    }
                }
            }
        }
    }
}