@import "main-module";

.match {

    &__container {
        margin-top: 60px;
        margin-bottom: 80px;

        &_second {
            margin-top: 0;
            margin-bottom: 100px;
        }
    }

    &__filter {
        display: flex;
        align-items: center;
        padding: 40px 0 20px;
        flex-wrap: wrap;
    
        @include below(1024px) {
            display: block;       
        }

        @include below(768px) {
            padding: 20px 0;
            border-bottom: 1px solid #DBDEE7;
        }
    }

    &__filter-group {
        display: flex;
        align-items: center;
        
        &:nth-of-type(1) {
            margin-right: 20px;

            @include below_and_above(1200px, 1025px) {
                width: 48%;
                order: 1;
                margin-bottom: 20px;
            }

            @include below(1024px) {
                margin-right: 0;
                margin-bottom: 20px;
            } 
            
            .match__filter-btn {
                @include below(420px) {
                    flex: auto;
                }
                &:nth-child(1) {
                    margin-right: 20px;

                    @include below(1024px) {
                        margin-right: 15px;
                    }
                }
            }
        }

        &:nth-of-type(2) {
            margin-right: auto;
            @include below_and_above(1400px, 1201px) {
                display: block;
            }

            @include below_and_above(1200px, 1025px)  {
                width: 48%;
                order: 3;
            }

            @include below(1024px) {
                margin-right: 0;
                margin-bottom: 20px;
            } 

            .match__filter-switch {
                &:nth-child(1) {
                    margin-right: 20px;
                    @include below_and_above(1400px, 1201px) {
                        margin-right: 0;
                        margin-bottom: 10px;
                    }
                }
            }
        }

        &:nth-child(3) {
            margin-right: 10px;

            @include below_and_above(1200px, 1025px) {
                order: 2;
                margin: 0 0 20px auto;
            }

            @include below(1024px) {
                margin-right: 0;
                margin-bottom: 20px;
            }

            .match__filter-control {
                margin-right: 20px;

                @include below(420px) {
                    margin-right: 18px;
                }
            }

            .select2.select2-container,
            .match__filter-select {
                .select2-selection--single {
                    height: 46.7px;
                }
                &:nth-of-type(1) {
                    min-width: 147px;
                    margin-right: 10px;
                }
            }
        }

        &:nth-of-type(4) {
            @include below_and_above(1200px, 1025px) {
                order: 4;
            }

            .match__filter-btn {
                @include below(1024px) {
                    margin-left: auto;
                }

                @include below(420px) {
                    flex: auto;
                }
            }
        }
    }

    &__filter-btn {
        

        svg {
            margin-right: 10px;
        }
    }

    &__filter-select {
        &.select ~ .select2-container .select2-selection--single {
            border-color: #DBDEE7;
        }
    }

    &__header {
        display: flex;
        align-items: center;
        padding: 12px 0;
        border-top: 1px solid #DBDEE7;
        border-bottom: 1px solid #DBDEE7;
        position: sticky;
        top: 0;
        z-index: 100;
        background-color: #fff;
        @include below(1024px) {
            display: none;
        }
    }

    &__header-item {
        font-size: 14px;
        font-weight: 600;
        line-height: normal;
        color: #000;
        
        display: flex;
        align-items: center;

        &:nth-child(1) {
            .match__header:not(.match__header_second) & {
                justify-content: center;
                width: 8%;
                @include below(1200px) {
                    width: 9.2%;
                }
            } 

            .match__header.match__header_second & {
                width: 10%;
            }

            .match__header.match__header_third & {
                justify-content: flex-start;
                width: 8%;
            }
        }

        &:nth-of-type(2) {
            width: 9%;
            @include below(1200px) {
                width: 10.2%;
            }

            .match__header.match__header_second & {
                width: 8%;
            }

            .match__header.match__header_third & {
                width: 10%;
            }
        }
        
        &:nth-of-type(3) {
            width: 12.6%;
            @include below(1200px) {
                width: 12.1%;
            }

            .match__header.match__header_third & {
                width: 45%;
            }
        }

        &:nth-of-type(4) {
            width: 39%;
            @include below(1200px) {
                width: 34.5%;
            }

            .match__header.match__header_second & {
                width: 23.5%;
            }

            .match__header.match__header_third & {
                width: 8%;
                justify-content: center;
            }
        }

        &:nth-of-type(5) {
            width: 7%;

            .match__header.match__header_second & {
                width: 12%;
            }
            .match__header.match__header_third & {
                width: 8%;
                justify-content: center;
            }
        }
        &:nth-of-type(6) {
            width: 15%;
            .match__header.match__header_second & {
                width: 10%;
            }
            .match__header.match__header_third & {
                width: 8%;
                justify-content: center;
            }
        }
        
        &:nth-of-type(7) {
            .match__header.match__header_second & {
                width: 15%;
            }

            .match__header.match__header_third & {
                width: 7%;
                justify-content: center;
            }
        }
        
        &:nth-of-type(8) {
            .match__header.match__header_third & {
                width: 7%;
                justify-content: center;
            }
        }

        &:nth-of-type(9) {
            .match__header.match__header_third & {
                width: 6%;
                justify-content: center;
            }
        }

        svg {
            width: 16px;
            height: 16px;
            margin-left: 2px;
        }
    }
    
    &__more {
        margin-top: 20px;
        display: flex;
        justify-content: center;
    }

    &__more-btn {
        padding: 14px 20px;
    }
}