@import "main-module";

.rates {

    &__title {
        font-size: 44px;
        font-weight: 700;
        margin-bottom: 40px;

        @include below(768px) {
            font-size: 32px;
            margin-bottom: 20px;
        }
    }

    &__row {
        padding: 20px 0 60px;
        display: flex;
        flex-wrap: wrap;
        align-items: stretch;
        margin: 0 -12px;

        @include below(1340px) {
            margin: -20px -12px 0;
        }

        @include below(540px) {
            padding: 20px 0 80px;
            margin: -20px 0 0;
        }
    }

    &__item {
        position: relative;
        width: calc(20% - 24px);
        margin: 0 12px;
        display: flex;
        flex-direction: column;
        padding: 40px 20px 20px 20px;
        border-radius: 10px;
        border: 1px solid $color_grey_light;
        background-color: $color_white;
        box-shadow: 0px 10px 20px -10px rgba(0, 0, 0, 0.15);
        transition: border-color .3s linear;

        &_active {
            border-color: $color_blue;
        }
        
        &:hover {
            border-color: $color_blue;
        }

        @include below(1340px) {
            width: calc(25% - 24px);
            margin: 20px 12px 0;
        }

        @include below(1090px) {
            width: calc(33.333% - 24px);
        }

        @include below(992px) {
            width: calc(50% - 24px);
        }

        @include below(540px) {
            width: 100%;
            margin: 20px 0 0;
        }
    }

    &__numb {
        position: absolute;
        top: 10px;
        right: 10px;
        padding: 2px 6px;
        background-color: $color_green;
        border-radius: 6px;
        color: $color_white;
        font-size: 10px;
        font-weight: 700;
    }

    &__top {
        display: flex;
        flex-direction: column;
        align-items: center;
        text-align: center;
        padding-bottom: 40px;
        border-bottom: 1px solid $color_grey_light;

        &-title {
            font-size: 18px;
            font-weight: 700;
            margin-bottom: 20px;
        }

        &-text {
            font-size: 14px;
            font-weight: 500;
            color: $color_grey;
            margin-bottom: 10px;
        }

        strong {
            font-size: 30px;
            font-weight: 800;
            color: $color_blue;
        }

        del {
            font-size: 16px;
            font-weight: 500;
            color: $color_grey;
        }
    }

    &__list {
        padding: 20px 0 40px;

        &_first {
            @include below(540px) {
                padding: 20px 0;
            }
        }
       
        &-item {
            display: flex;
            align-items: center;
            font-size: 14px;
            font-weight: 500;
            line-height: 140%;

            &:not(:last-child) {
                margin-bottom: 14px;
            }

            svg {
                width: 20px;
                height: 20px;
                margin-right: 10px;
            }
        }
    }

    &__btn {
        margin-top: auto;
        width: 100%;
    }
}