@import "main-module";

.radio-button {
    &__wrap {
        display: inline-block;
        cursor: pointer;
        input {
            position: absolute;
            top: 0;
            left: -1000%;

            &:checked ~ .radio-button__mark {
                border-color: #416FF4;
                &::before {
                    background-color: #416FF4;
                }
            }

            &:disabled {
                & ~ .radio-button__mark {
                    background-color: #F7F9FB;
                    border-color: #F7F9FB;
                    color: #808191;
                }

                & ~ .radio-button__text {
                    color: #808191;
                }
            }
        }
    }

    &__mark {
        display: flex;
        justify-content: center;
        align-items: center;

        width: 20px;
        height: 20px;
        border: 1px solid #DBDEE7;
        border-radius: 50%;
        transition: border-color .3s linear;

        &::before {
            content: "";
            width: 12px;
            height: 12px;
            display: block;
            border-radius: 50%;
            background-color: transparent;
            transition: background-color .3s linear;
        }
    }
}