@import "main-module";

.btn {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 14px 20px;
    border-radius: 10px;
    font-size: 14px;
    font-weight: 700;
    border: 1px solid $color_blue;
    color: $color_blue;
    transition: background-color .3s linear, color .3s linear, border-color .3s linear;

    @include below(400px) {
        font-size: 12px;
        padding: 14px 8px;
    }

    svg {
        width: 16px;
        height: 16px;
        margin-right: 2px;
        transition: fill .3s linear;
        margin-right: 2px;

        @include below(400px) {
            width: 14px;
            height: 14px;
        }
    }

    &:hover:not(:active) {
        background-color: $color_blue;
        color: $color_white;
        border-color: transparent;
    }

    &_blue {
        background-color: $color_blue;
        color: $color_white;

        &:hover:not(:active) {
            background-color: $color_white;
            color: $color_blue;
            border-color: $color_blue;
            svg {
                fill: #416FF4;
            }
        }
    }

}