@import "main-module";

.entrance {
    padding: 100px 0;
    &__wrap {
        display: flex;
        align-items: center;
        flex-direction: column;
    }

    &__logo {
        display: flex;
        align-items: center;
        color: $color_black;
        text-transform: uppercase;
        font-weight: 800;
        font-size: 30px;
        margin-bottom: 77px;

        @include below(768px) {
           margin-bottom: 60px;
        }

        svg {
            width: 55px;
            height: 50px;
            margin-right: 9px;
        }
    }

    &__box {
        width: 100%;
        max-width: 416px;
        background-color: $color_white;
        padding: 40px;
        border: 1px solid #F7F9FB;
        border-radius: 20px;
        box-shadow: 0px 10px 20px -10px rgba(0, 0, 0, 0.15);
        display: flex;
        flex-direction: column;
        margin-bottom: 40px;

        @include below(768px) {
            padding: 40px 20px;
        }
    }

    &__title {
        font-size: 24px;
        font-weight: 700;
        margin-bottom: 40px;
        text-align: center;
    }

    &__input {
        width: 100%;
        margin-bottom: 20px;

        .input {
            width: 100%;
        }
    }

    &__btn {
        margin-top: 20px;

        &_second {
            margin-top: 0;
        }
    }

    &__text {
        font-size: 14px;
        font-weight: 500;

        &:first-of-type {
            margin-bottom: 10px;
        }

        a {
            color: $color_blue;
            text-decoration: underline;
            transition: text-decoration .3s liner;

            &:hover {
                text-decoration: none;
            }
        }
    }
}