@import "main-module";

.forks {

    &__title {
        @include title;
        margin-bottom: 20px;
    }

    &__board {
        background-color: #416FF4;
        border-radius: 20px;
        padding: 20px 40px;

        display: flex;
        justify-content: space-between;
        align-items: center;
        color: #fff;
        margin-bottom: 20px;

        @include below(1024px) {
            align-items: initial;
            flex-direction: column;
            padding: 20px;
        }
    }

    &__board-item {
        position: relative;
        &:nth-last-of-type(1) {

            @include below(1024px) {
                margin-top: 20px;
            }

            &::before {
                content: "";
                position: absolute;
                bottom: 30px;
                left: -90.6%;
                width: 85%;
                border-bottom: 1px dashed #fff;

                @include below(1400px) {
                    width: 45%;
                    left: -54.5%;
                }

                @include below(1200px) {
                    content: none;
                }
            }
        }
    }

    &__board-title {
        font-size: 24px;
        font-weight: 700;
        line-height: 120%;
        max-width: 356px;

        @include below(1200px) {
            font-size: 20px;
            max-width: 280px;
        }

        @include below(1024px) {
            max-width: initial;
            margin-bottom: 30px;
        }
    }

    &__board-label {
        font-size: 16px;
        line-height: normal;
    }

    &__board-num {
        font-weight: 800;
        font-size: 54px;

        @include below_and_above(1200px, 1025px) {
            font-size: 32px;
        }
    }

    &__info {
        display: flex;
        align-items: center;
        padding: 20px;
        border-radius: 20px;
        @include below(1024px) {
            align-items: flex-start;
            padding: 15px;
        }

        &_danger {
            color: #DE3618;
            background-color: #FCEBE8;
        }

        &_warn {
            color: #FFA234;
            background-color: #FFF6EB;
        }
    }

    &__info-text {
        font-size: 16px;
        font-weight: 600;
        line-height: normal;
        color: inherit;
        margin-right: 10px;
        a {
            color: inherit;
            font-weight: 800;
            text-decoration-line: underline;
        }
    }

    &__info-icon {
        margin-right: 10px;
        svg {
            width: 24px;
            height: 24px;
        }
    }

    &__info-btn {
        margin-left: auto;
        svg {
            width: 24px;
            height: 24px;
        }
    }
}