@import "main-module";

.tooltip {
    position: absolute;
    top: 110%;
    left: 50%;
    z-index: 100;
    transform: translateX(-50%);
    white-space: nowrap;
    border: 1px solid #F7F9FB;
    box-shadow: 0px 10px 20px -10px rgba(0, 0, 0, 0.15);
    border-radius: 10px;
    background-color: #fff;
    font-size: 12px;
    font-weight: 400;
    line-height: 140%;
    color: #000;
    padding: 10px;
    z-index: -10000000;
    opacity: 0;
    transition: opacity .3s linear;
}