@import "main-module";

.match-card {
    &__wrapper {
        display: flex;
        border-bottom: 1px solid #000;
        padding: 28px 0;

        @include below(1024px) {
            flex-direction: column;
            padding: 20px 0;
        }
    }

    &__col {
        &:nth-of-type(1) {
            display: flex;
            width: 16.04%;
            margin-right: 12px;
            @include below(1200px) {
                width: 20%;
            }

            @include below(1024px) {
                width: 100%;
                margin-right: 0;
                margin-bottom: 10px;
            }

            .match-card__wrapper.match-card__wrapper_second & {
                @include above(1025px) {
                    width: 17.04%;
                }
            }

            .match-card__wrapper.match-card__wrapper_third & {
                @include above(1025px) {
                    width: 6.5%;
                }
            }
        }

        &:nth-of-type(2) {
            width: 70%;
            margin-right: 12px;
            @include below(1024px) {
                width: 100%;
                margin-right: 0;
                margin-bottom: 21px;
            }

            .match-card__wrapper.match-card__wrapper_third & {
                flex: auto;
                width: initial;
                margin-right: 0;

                @include below(1024px) {
                    margin-bottom: 0;
                }
            }
        }
    }

    &__row {
        display: flex;
        align-items: flex-start;

        @include below(1024px) {
            display: block;
        }

        &:not(:last-child) {
            margin-bottom: 10px;

            @include below(1024px) {
                margin-bottom: 20px;
            }
        }

        .match-card__wrapper.match-card__wrapper_third & {
            flex: auto;
        }
    }
    

    &__income {
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 48%;
        margin-right: 2%;
        .match-card__wrapper.match-card__wrapper_second & {
            @include above(1025px) {
                width: 56%;
            }
        }

        @include below(1024px) {
            flex: auto;
            width: auto;
            flex-direction: row;
            margin-right: 12px;
        }

        
    }

    &__income-num {
        font-size: 16px;
        font-weight: 900;
        line-height: normal;
        color: #416FF4;
        margin-bottom: 4px;

        @include below(1024px) {
            width: 50px;
            min-width: 50px;
            margin-right: 10px;
        }
    }

    &__income-time {
        display: flex;
        align-items: center;

        font-size: 12px;
        font-weight: 500;
        line-height: normal;
        color: #808191;
        margin-bottom: 5px;
        @include below(1024px) {
            width: 70px;
            min-width: 70px;
            margin-right: 10px;
            margin-bottom: 0;
        }
        svg {
            width: 14px;
            height: 14px;
            margin-left: 2px;
        }
    }

    &__income-badge {
        font-size: 9px;
        font-weight: 700;
        line-height: normal;
        text-transform: uppercase;
        color: #fff;
        padding: 2px 6px;
        background-color: #22C55E;
        border-radius: 6px;
    }

    &__date {
        font-size: 14px;
        font-weight: 500;
        line-height: normal;
        color: #000;

        @include below(1024px) {
            font-size: 12px;
            br {
                display: none;
            }
        }
    }

    &__bookmaker {
        font-size: 14px;
        font-weight: 500;
        line-height: normal;
        color: #000;

        display: flex;
        align-items: center;

        width: 17%;
        margin-right: 12px;

        .match-card__wrapper.match-card__wrapper_third & {
            @include above(1025px) {
                width: 9.2%;
            }      
        }

        @include below(1024px)  {
            width: auto;
            margin-bottom: 6px;
        }

        svg {
            width: 14px;
            height: 14px;
            margin-left: 2px;
        }
    }
    &__event {
        width: 55%;
        margin-right: 12px;
        line-height: normal;
        .match-card__wrapper.match-card__wrapper_second & {
            @include above(1025px) {
                width: 32%;
            }
        }

        .match-card__wrapper.match-card__wrapper_third & {
            @include above(1025px) {
                width: 45%;
            }        
        }

        @include below(1200px) {
            width: 50%;
        }

        @include below(1024px) {
            width: auto;
            margin-bottom: 7px;
        }

        & > * {
            display: block;
        }

        strong {
            font-size: 14px;
            font-weight: 700;
            color: #000;
            margin-bottom: 4px;
        }
        span {
            font-size: 12px;
            font-weight: 400;
            color: #808191;
        }
    }

    &__chart {
        width: 26.22%;
        display: flex;
        align-items: center;
    
        @include below(1024px) {
            width: auto;
            margin: 0 -12px;
        }

        .match-card__wrapper.match-card__wrapper_second & {
            width: 47%;
            @include below(1024px) {
                width: auto;
                flex-direction: column;
                align-items: flex-start;
                margin: 0;

                .match-card__chart-item {
                    width: auto;
                    margin: 0;
                    &:not(:last-child) {
                        margin-bottom: 6px;
                    }
                }                
            }
        }

        .match-card__wrapper.match-card__wrapper_third & {
            flex: auto;
        }
    }

    &__detail {
        padding: 10px;
    }

    &__detail-item {
        display: flex;
        align-items: center;

        &:not(:last-child) {
            margin-bottom: 12px;
        }
    }

    &__detail-num {
        display: flex;
        align-items: center;
        font-size: 14px;
        font-weight: 500;
        line-height: normal;

        svg {
            margin-left: 2px;
            width: 14px;
            height: 14px;
        }
    }

    &__detail-text {
        font-size: 12px;
        font-weight: 500;
        line-height: normal;
        color: #808191;
        margin-left: 10px;
    }

    &__chart {
        .match-card__wrapper.match-card__wrapper_third & {
            margin: 20px 0 0;
        }
    }

    &__chart-item {
        display: flex;
        align-items: center;
        
        @include below(1024px) {
            width: 50%;
            margin: 0 12px;
        }

        .match-card__wrapper.match-card__wrapper_third & {
            @include below(1024px) {
                flex: auto;
                width: initial;
                margin: 0;
                .match-card__chart-label {
                    margin-right: 0;
                    margin-bottom: 10px;
                }
                flex-direction: column;
            }
        }

        &:nth-of-type(1) {
            width: 47%; 
            margin-right: 12px;
            @include below(1024px) {
                width: 40%;
            }

            .match-card__wrapper.match-card__wrapper_second & {
                width: 34%;
            }

            .match-card__wrapper.match-card__wrapper_third & {
                @include above(1025px) {
                    width: 18%;
                    justify-content: center;
                }
            }
        }

        &:nth-of-type(2) {
            .match-card__wrapper.match-card__wrapper_second & {
                width: 30%;
            }
            .match-card__wrapper.match-card__wrapper_third & {
                @include above(1025px) {
                    width: 18%;
                    justify-content: center;
                }
            }
        }

        &:nth-of-type(3) {
            .match-card__wrapper.match-card__wrapper_second & {
                justify-content: center;
                flex: auto;
            }

            .match-card__wrapper.match-card__wrapper_third & {
                @include above(1025px) {
                    width: 18%;
                    justify-content: center;
                }
            }
        }

        &:nth-of-type(4) {
            .match-card__wrapper.match-card__wrapper_third & {
                @include above(1025px) {
                    width: 16%;
                    justify-content: center;
                }
            }
        }


        &:nth-of-type(5) {
            .match-card__wrapper.match-card__wrapper_third & {
                @include above(1025px) {
                    width: 14%;
                    justify-content: center;
                }
            }
        }

        &:nth-of-type(6) {
            .match-card__wrapper.match-card__wrapper_third & {
                @include above(1025px) {
                    width: 15%;
                    justify-content: center;
                }
            }
        }
    }

    &__chart-label {
        font-weight: 400;
        line-height: normal;
        color: #808191;
        margin-right: 10px;
        font-size: 12px;

        @include above(1025px) {
            display: none;
        }

        @include below(370px) {
            font-size: 10px;
        }
    }

    &__chart-value {
        font-size: 14px;
        font-weight: 500;
        line-height: normal;
        color: #000;

        display: flex;
        align-items: center;
        cursor: pointer;
            position: relative;
            &:hover {
                .tooltip {
                    z-index: 100;
                    opacity: 1;
                }
            }
            .tooltip {
                white-space: normal;
                width: 200px;
                text-align: center;
                
            }

            .match-card__chart-item:nth-child(2) & {
                &::before {
                   left: auto;
                   right: 0; 
                }
            }
        &_hidden {
            font-weight: 600;
            text-decoration-line: underline;
            color: #416FF4;
            
        }
        
        @include below(1024px) {
            white-space: nowrap;
        }
        @include below(370px) {
            font-size: 12px;
        }
        svg {
            width: 14px;
            height: 14px;
            margin-left: 2px;
            @include below(370px) {
                width: 12px;
                height: 12px;
            }
        }
    }

    &__chart-icon {
        width: 14px;
        min-width: 14px;
        height: 14px;
    }

    &__action {
        @include below(1024px) {
            display: flex;
            flex-direction: row-reverse;
            justify-content: space-between;
        }
    }

    &__action-more {
        padding: 10px;
        border-radius: 10px;
        margin-bottom: 6px;
        white-space: nowrap;
    }

    &__action-bottom {
        display: flex;
        align-items: center;
        justify-content: center;

        @include below(1024px) {
            justify-content: flex-start;
        }
    }

    &__action-btn {
        width: 32px;
        height: 32px;
        background-color: #fff;
        border: 1px solid #DBDEE7;
        border-radius: 50%;

        display: flex;
        justify-content: center;
        align-items: center;
        transition: background-color .3s linear, border-color .3s linear, color .3s linear;
        color: #808191;
        &:not(:last-child) {
            margin-right: 6px;
        }

        &:hover:not(:active) {
            color: #fff;
            background-color: #416FF4;
            border-color: #416FF4;
        }
        svg {
            width: 18px;
            height: 18px;
        }
    }
}