@mixin title() {
    font-size: 44px;
    font-weight: 700;
    line-height: normal;

    @include below(1024px) {
        font-size: 32px;
    }
}

@mixin tooltip() {
    position: relative;
    &:hover {
        &::before {
            z-index: 100;
            opacity: 1;
        }
    }
    
    &::before {
        content: attr(data-tooltip);
        position: absolute;
        bottom: -40px;
        left: 50%;
        z-index: 100;
        transform: translateX(-50%);
        white-space: nowrap;
        border: 1px solid #F7F9FB;
        box-shadow: 0px 10px 20px -10px rgba(0, 0, 0, 0.15);
        border-radius: 10px;
        background-color: #fff;
        font-size: 12px;
        font-weight: 400;
        line-height: 140%;
        color: #000;
        padding: 10px;
        z-index: -10000000;
        opacity: 0;
        transition: opacity .3s linear;

        @include below(1024px) {
            left: 0;
            transform: translateX(0);
        }
    }
}

