@mixin block-padding() {
    padding: 60px 0;

    @include below(1024px) {
        padding: 30px 0;
    }

}

@mixin below($width) {
    @media (max-width: $width) {
      @content;
    }
}

@mixin above($width) {
    @media (min-width: $width) {
        @content;
    }
}

@mixin below_and_above($max, $min) {
    @media (max-width: $max) and (min-width: $min) {
        @content;
    }
}


