
@import "main-module";

.benefits {
    overflow: hidden;
    padding: 60px 0;
    background-color: $color_blue;
    position: relative;

    @include below(768px) {
        padding: 60px 0 80px;
    }

    &__img {
        position: absolute;
        top: -24%;
        left: -1%;
        z-index: 0;

        @include below(992px) {
            top: -20%;
            left: -15%;
        }

        @include below(768px) {
            top: -50%;
            left: -40%;
        }

        @include below(540px) {
            width: 400px;
            height: 200px;
            top: -35%;
            left: 0;
        }

        @include below(540px) {
            width: 350px;
            height: 200px;
            top: -20%;
            left: -20%;
        }

    }

    &__wrap {
        position: relative;
        display: flex;
        flex-wrap: wrap;
        align-items: stretch;
        margin: -12px;

        @include below(768px) {
            margin: 0;
        }
    }

    &__content {
        width: 100%;
        max-width: 455px;
        color: $color_white;
    }

    &__wrap-img {
        position: absolute;
        top: 35px;
        right: 34%;
        z-index: 0;

        @include below(1240px) {
            right: -10%;
        }

        @include below(768px) {
            display: none;
        }
    }

    &__title {
        font-size: 44px;
        font-weight: 700;
        margin-bottom: 20px;

        @include below(1024px) {
            font-size: 32px;
        }
    }

    &__text {
        color: #F7F9FB;
        font-size: 18px;
        font-weight: 500;
        line-height: 160%;
      
    }

    &__item {
        width: calc(25% - 24px);
        margin: 12px;
        display: flex;
        flex-direction: column;
        background-color: $color_white;
        padding: 30px;
        border-radius: 20px;
        border: 1px solid  #F7F9FB;
        box-shadow: 0px 10px 20px -10px rgba(0, 0, 0, 0.15);
        z-index: 1;
        @include below(1240px) {
            width: calc(33.333% - 24px);
        }

        @include below(992px) {
            width: calc(50% - 24px);
        }

        @include below(768px) {
           display: none;
        }

        &_first {
            display: flex;
            flex-direction: column;
            justify-content: center;
            color: $color_white;
            width: calc(50% - 24px);
            margin: 12px;
            padding: 0;
            background-color: transparent;
            border: none;
            box-shadow: none;
            border-radius: 0;

            @include below(1240px) {
                width: 100%;
                margin: 0 12px 12px;
            }

            @include below(992px) {
                width: 100%;
                margin: 0 12px 12px;
            }

            @include below(768px) {
                display: block;
                margin: 0 0 40px;
            }
        }
   
      
    }

    &__icon {
        width: 70px;
        height: 70px;
        flex-shrink: 0;
        display: flex;
        align-items: center;
        justify-content: center;
        margin-bottom: 60px;
        background-color: $color_blue;
        border-radius: 50%;

        svg {
            width: 34px;
            height: 34px;
        }
    }

    &__item-title {
        font-size: 20px;
        font-weight: 700;
        line-height: 140%;
        margin-bottom: 20px;
    }

    &__item-text {
        font-size: 16px;
        font-weight: 500;
        line-height: 140%;
        color: $color_grey;
    }

    &__row {

        &.swiper {
            display: none; 
            @include below(768px) {
                display: flex;
                margin: -30px 0;
                padding: 30px 0;
            }
        }

        .swiper-wrapper {
            display: flex;
            align-items: stretch;

            .benefits__item.swiper-slide {
                display: flex;
                flex-direction: column;
                height: initial;
                margin: 0;
            }
        }

        .swiper-pagination {
            &.swiper-pagination-horizontal {
                bottom: 0;
            }
            .swiper-pagination-bullet {
                width: 8px;
                height: 8px;
                margin: 0 5px;
                background-color: rgba(255, 255, 255, 0.20);
                opacity: 1;
                transition: transform .3s linear;
            }

            .swiper-pagination-bullet-active {
                transform: scale(1.3);
                background-color: $color_white;
            }
        }
    }
}