@import "main-module";

.card {
    display: flex;
    flex-direction: column;
    border-radius: 10px;
    border: 1px solid  #F7F9FB;
    background-color: $color_white;
    box-shadow: 0px 10px 20px -10px rgba(0, 0, 0, 0.15);
    padding: 20px;

    &__img {
        img {
            width: 100%;
        }
    }

    &__body {
        display: flex;
        flex-direction: column;
        padding-top: 20px;

        &-top {
            display: flex;
            align-items: center;
            justify-content: space-between;
            margin-bottom: 4px;  
        }

        &-right {
            display: flex;
        }
    }

    &__data {
        font-size: 12px;
        font-weight: 400;
        line-height: 140%;
        color: $color_grey;
    }

    &__badge {
        font-size: 12px;
        font-weight: 600;
        padding: 10px;
        border-radius: 10px;
        color: $color_grey;
        background-color: #F7F9FB;

        &:nth-of-type(2) {
            margin-left: 2px;
        }
    }

    &__title {
        color: $color_black;
        font-size: 20px;
        font-weight: 700;
        line-height: 120%;
        margin-bottom: 10px;
    }

    &__text {
        font-size: 14px;
        font-weight: 500;
        line-height: 150%;
        color: $color_grey;
        overflow: hidden;
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 3;

        &_second {
            -webkit-line-clamp: 4;
        }

        &_third {
            -webkit-line-clamp: 5;
        }
    }
}