@import "main-module";


.calculator {
    margin-top: 100px;
    background-color: #fff;
    box-shadow: 0px -10px 40px -10px rgba(0, 0, 0, 0.15);
    padding: 52px 0 40px;
    border: 1px solid #F7F9FB;

    @include below(1340px) {
        padding: 32px 20px;
    }

    @include below(1024px) {
        display: none;
    }

    &__wrapper {
        max-width: 1218px;
        margin: 0 auto;
    }

    &__head {
        display: flex;
        justify-content: space-between;
        margin-bottom: 40px;
    }

    &__head-item {
        &:not(:last-child) {
            margin-right: 12px;
        }

        &.calculator__head-item_second {
            display: flex;
            align-items: center;
        }
    }

    &__head-select {
        display: flex;
        align-items: center;

        &:first-child {
            margin-right: 40px;
            .select2-container {
                width: 96px;
            }
        }
        &:last-child {
            .select2-container {
                width: 186px;
            }
        }
        
        strong {
            font-family: Raleway;
            font-size: 14px;
            font-weight: 600;
            line-height: normal;
            margin-right: 6px;
        }
    }
 
    &__head-title {
        font-family: Raleway;
        font-size: 24px;
        font-weight: 700;
        line-height: normal;
        color: #000;
        margin-bottom: 10px;
    }

    &__head-text {
        font-family: Raleway;
        font-size: 14px;
        font-weight: 400;
        line-height: normal;
        color: #808191;
    }

    &__head-num {
        font-family: Raleway;
        font-size: 24px;
        font-weight: 700;
        line-height: normal;
        
        margin-bottom: 10px;

        &.is-red {
            color: #DE3618;
        }
    }

    &__header {
        display: flex;
        align-items: center;
        padding: 6px 0;
        border-bottom: 1px solid #DBDEE7;
    }

    &__header-item {
        font-family: Raleway;
        font-size: 12px;
        font-weight: 500;
        line-height: normal;
        color: #808191;


        &:nth-child(1) {
            width: 13.02%;
            margin-right: 2%;

            @include below(1280px) {
                width: 8%;
            }
        }
        &:nth-child(2) {
            width: 11.30%;
            margin-right: 2%;

            @include below(1280px) {
                width: 8%;
            }
        }

        &:nth-child(3) {
            width: 14.25%;
            margin-right: 2%;

            @include below(1280px) {
                width: 16%;
            }
        }
        &:nth-child(4) {
            width: 7.44%;
            margin-right: 2%;

            
        }
        &:nth-child(5) {
            width: 16.39%;
            margin-right: 2%;

            @include below(1280px) {
                width: 18%;
            }
        }
        &:nth-child(6) {
            width: 10.72%;
            margin-right: 2%;
        }
        &:nth-child(7) {
            width: 0.28%;
            margin-right: 3%;
        }
        &:nth-child(8) {
            width: 0.28%;
            margin-right: 6%;
        }
    }

    &__row {
        display: flex;
        align-items: center;
        padding: 10px 0;
        &:not(:last-of-type) {
            border-bottom: 1px solid #DBDEE7;
        }
    }

    &__row-item {
        .calculator__row:not(.calculator__row_second) & {
            &:nth-child(1) {
                width: 13.02%;
                margin-right: 2%;

                @include below(1280px) {
                    width: 8%;
                }
            }
            &:nth-child(2) {
                width: 11.30%;
                margin-right: 2%;
            
                @include below(1280px) {
                    width: 8%;
                }
            }
    
            &:nth-child(3) {
                width: 14.25%;
                margin-right: 2%;
                @include below(1280px) {
                    width: 16%;
                }
            }
    
            &:nth-child(4) {
                width: 6.44%;
                margin-right: 2%;
            }
    
            &:nth-child(5) {
                width: 15.39%;
                margin-right: 2%;
                @include below(1280px) {
                    width: 17%;
                }
            }
            &:nth-child(6) {
                width: 11.2%;
                margin-right: 3%;

                .calculator__input {
                    @include below(1280px) {
                        min-width: 45px;
                    }
                }
            }
            &:nth-child(7) {
                width: 1.8%;
                margin-right: 1.5%;
            }
    
            &:nth-child(8) {
                width: 1.8%;
                margin-right: 5%;
            }
        }

        
        .calculator__row.calculator__row_second & {
            &:nth-child(1) {
                width: 51%;
                margin-right: 2%;

                @include below(1280px) {
                    width: 44.5%;
                }
            }

            &:nth-child(2) {
                width: 15.39%;
                margin-right: 16.2%;

                @include below(1280px) {
                    width: 17%;
                }
            }

            &:nth-child(3) {
                width: 1.8%;
                margin-right: 1.5%;
            }

            &:nth-child(4) {
                width: 1.8%;
                margin-right: 1.5%;
            }
        }

        strong {
            font-family: Raleway;
            font-size: 14px;
            font-weight: 600;
            line-height: normal;
            color: #000;
            &.red {
                color: #DE3618;
            }
        }
    }

    &__inputs {
        display: flex;
        align-items: center;
        strong {
            margin-right: 4px;
        } 
    }

    &__input {
        &:not(:last-child) {
            margin-right: 10px;
        }
        .input {
            width: 100%;

            @include below(1280px) {
                padding: 14px 8px;
            }
        }
    }

    &__link {
        display: flex;
        align-items: center;
        font-family: Raleway;
        font-size: 14px;
        font-weight: 600;
        line-height: normal;
        color: #416FF4;
        svg {
            width: 18px;
            height: 18px;
            margin-left: 4px;
        }
    }

    &__copy {
        display: inline-flex;
        align-items: center;
        svg {
            width: 16px;
            height: 16px;
            margin-right: 10px;
        }
    }

    &__bottom {
        display: flex;
        align-items: center;
        margin-top: 10px;
    }

    &__bottom-item {
        &:not(:last-child) {
            margin-right: 3.28%; 
        }
    }

    &__bottom-flex {
        display: flex;
        align-items: center;
        
        .calculator__checkbox {
            margin-right: 6px;
        }
        .calculator__input {
            max-width: 80px;
        }
    }
}