@import "main-module";

.footer {
    background-color: $color_black;

    &__top {
        padding: 40px 0;
        border-bottom: 1px solid rgba(255, 255, 255, 0.20);

        @include below(768px) {
            padding: 20px 0 40px;
        }
    }

    &__left {
        margin-right: 80px;

        @include below(768px) {
            display: flex;
            justify-content: space-between;
            margin: 0 0 40px;
        }
    }

   

    &__wrap {
        display: flex;
        
        @include below(768px) {
            flex-direction: column;
        }
    }

    &__logo {
        display: flex;
        align-items: center;
        margin-bottom: 35px;
        font-size: 26px;
        font-weight: 800;
        text-transform: uppercase;
        color: $color_white;

        @include below(768px) {
           margin-bottom: 20px;
        }
        svg {
            width: 27px;
            height: 25px;
            flex-shrink: 0;
            margin-right: 5px;
        }
    }

    &__link {
        font-size: 14px;
        font-weight: 700;
        color: $color_grey_light;
        transform: color .3s linear;
        &:hover:not(:active) {
            color: $color_blue;
        }
    }

    &__socials {
        display: flex;
        margin: 27px 0 20px;

        @include below(768px) {
            margin: 10px 0 0;
        }
        &-link {
            &:first-of-type {
                margin-right: 10px;
            }
            svg {
                width: 24px;
                height: 24px;
            }
        }
    }

    &__select {
        .select {
            padding: 8px 10px;
            height: 32px;
            width: 56px;
            background-color: transparent;
            & ~ .select2-container {
                .select2-selection--single {
                    padding: 8px 10px;
                    height: 32px;
                    width: 56px;
                    background-color: transparent;
                    border: 1px solid $color_grey;
    
                    .select2-selection__arrow {
                        background-image: url("../img/sprites/sprite.svg#arrow_white"); 
                    }
                }
            }
        } 
    }

    &__row {
        display: flex;
        justify-content: space-between;
        flex: auto;
        
        @include below(1100px) {
            justify-content: initial;
            flex-wrap: wrap;
            padding: -20px -55px 0;
        }

        @include below(992px) {
            margin: -20px -30px 0;
        }

        @include below(768px) {
            margin: -30px 0 0;
        }
    }

    &__col {
        @include below(1100px) {
            flex-wrap: wrap;
            margin: 20px 55px 0;
        }

        @include below(992px) {
            width: calc(50% - 60px);
            margin: 20px 30px 0;
        }

        @include below(768px) {
            width: 100%;
            margin: 20px 0 0;
        }

        &-head {
            display: flex;
            align-items: center;
            justify-content: space-between;
            margin-bottom: 20px;

            @include below(768px) {
                margin-bottom: 10px;
            }

            &_active {
                .footer__col-icon {
                    svg {
                        transform: rotate(180deg);
                    }
                }
            }
        }

        &-title {
            font-size: 16px;
            font-weight: 700;
            color: $color_white;
        }

        &-icon {
            display: none;
            @include below(768px) {
                display: block;
            }
            svg {
                width: 24px;
                height: 24px;
                transition: transform .3s linear;
            }
        }
    }

    &__list {
        transition: max-height .3s linear, opacity .4s linear, overflow .4s linear;
        @include below(768px) {
            max-height: 0;
            opacity: 0;
            overflow: hidden;
        }
        
        &-item {
            &:not(:last-of-type) {
                margin-bottom: 10px;
            }
        }

        &-link {
            font-size: 14px;
            font-weight: 500;
            color: $color_grey_light;
            transform: color .3s linear;
            &:hover:not(:active) {
                color: $color_blue;
            }
        }
    }

    &__bottom {
        padding: 20px 0;

        @include below(768px) {
            padding: 20px 0 15px;
        }
    }

    &__copy {
        text-align: center;
        font-size: 14px;
        font-weight: 500;
        color: $color_grey_light;
    }
}